import React, { Component } from "react"
import { Link } from "gatsby";
import "materialize-css/dist/css/materialize.min.css";
import "../styles/global.css";
import Nav from "../components/navigation/Nav";

class Cbt extends Component {
    render() {
        return (
            <>
                <Nav />
                <div className="container">
                    <h4 className="center-align">INSTRUCTIONS</h4>
                    <table className="striped">
                        <tbody>
                            <tr>
                                <td>Questions answered will be indicated by a green button <button className="btn" style={{ backgroundColor: "rgb(90, 209, 90)" }}>1</button> while unanswered questions will be indicated by a white button <button className="btn btn-flat">1</button></td>
                            </tr>
                            <tr>
                                <td>Only submit once you have answered the questions you can or choose to answer because once you have submitted it cannot be reversed</td>
                            </tr>
                            <tr>
                                <td>Try not to <span style={{ color: "rgb(235, 68, 57)", fontWeight: "bold" }}>CHEAT</span> during this exam. it's for your own good</td>
                            </tr>
                            <tr>
                                <td>Do not refresh the page during the exam or else you will loose your progress</td>
                            </tr>
                            <tr>
                                <td>Upon submission your score will be shown you</td>
                            </tr>
                            <tr>
                                <td>Select your appropriate class below to begin <span style={{ color: "green", fontWeight: "bold" }}>GOOD LUCK</span></td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="row  center-align">
                        <Link to="/cbt/primary"><button className="btn waves-effect wave-light">Primary</button></Link>
                    </div>
                    <div className="row  center-align">
                        <Link to="/cbt/junior"><button className="btn waves-effect wave-light ">Junior Secondary</button></Link>
                    </div>
                    <div className="row  center-align">
                        <Link to="/cbt/senior" className="center-align"><button className="btn waves-effect wave-light ">Senior Secondary</button></Link>
                    </div>
                </div>
            </>
        );
    }
}

export default Cbt;